<template>
  <div>
    <vx-header :showback="1" :showrrefresh="1" :title="$t('settings.enterConfig')" @refresh="refresh"></vx-header>
    <div class="vx-container">
      <div class="setting_cont">
        <!-- 选择源 -->
        <vx-cell :title="$t('settings.enterSource')" :value="sourceValue" @link="selectSource"></vx-cell>
        <!-- 选择分辨率 -->
        <vx-cell :title="$t('settings.resolution')" :value="rateValue" @link="selectRate" v-if="isShowResolution"></vx-cell>
        <div class="progress-list">
          <div class="mask">{{$t('settings.QualityRegulation')}}</div>
          <vx-progress :class="o.class" v-for="(o,i) in definitionConfig" :title="o.title" :num="o.value" :key="i" @touchend="save" @update="val=>o.value=val" :range="o.range" :step="o.step"></vx-progress>

          <div class="btns">
            <div class="m-btn yellow-btn" @click="reset">{{$t('common.default')}}</div>
          </div>
          <div class="footer"></div>
        </div>

      </div>
    </div>
  </div>
</template>
<script>
  import htp from "../../api/http";
  import urls from "../../api/apiurl";
  import { serialize, setLoc, getLoc } from 'src/utils';
  import { Indicator } from 'mint-ui';
  import { mapGetters } from 'vuex';
  export default {
    data() {
      return {
        definition: {
          light: {
            title: this.$t('common.light'),
            value: 50,
            step: 1,
            class: '',
            range: {
              start: 0,
              end: 100
            }
          },
          contrast: {
            title: this.$t('common.contrast'),
            value: 50,
            step: 1,
            class: '',
            range: {
              start: 0,
              end: 100
            }
          },
          saturability: {
            title: this.$t('common.saturability'),
            value: 50,
            step: 1,
            class: '',
            range: {
              start: 0,
              end: 100
            }
          },
          hue: {
            title: this.$t('common.hue'),
            value: 50,
            step: 1,
            class: '',
            range: {
              start: -180,
              end: 180
            }
          },
          Rvalue: {
            title: this.$t('common.Rvalue'),
            value: 50,
            step: 1,
            class: 'pred',
            range: {
              start: 0,
              end: 100
            }
          },
          Gvalue: {
            title: this.$t('common.Gvalue'),
            value: 50,
            step: 1,
            class: 'pgreen',
            range: {
              start: 0,
              end: 100
            }
          },
          Bvalue: {
            title: this.$t('common.Bvalue'),
            value: 50,
            step: 1,
            class: 'pblue',
            range: {
              start: 0,
              end: 100
            }
          }
        },
        sources: [],
        selectedSource: {
          name: ''
        },//选中的源
        ids: [25089, 25090, 25091],
        rateValue: '',
        getedRateValue: '',
        req: {
          sourceId: '',
          resolutionId: ''
        },
        isShowResolution: true
      };
    },
    computed: {
      ...mapGetters(['getSelectedRate']),
      definitionConfig: function() {
        return Object.values(this.definition);
      },
      sourceValue() {
        if(this.selectedSource.name !== '') {
          if(this.selectedSource.status === 0) {
            console.log(2);
            return this.selectedSource.name + '(' + this.$t('common.Nosignal') + ')';
          } else {
            return this.selectedSource.name + '(' + this.getedRateValue + ')';
          }
        } else {
          return '';
        }
      }
    },
    mounted() {
      if(!window.offline) {
        Indicator.open({
          text: getLoc('lang') == 'en' ? 'Loading...' : '加载中...',
          spinnerType: 'triple-bounce'
        });
      }
      this.initEnterConfig();
    },
    methods: {
      initEnterConfig() {
        this.handler();
      },
      handler() {
        htp.get(urls.getInputSourceList).then(response => {
          Indicator.close();
          if(response.status === 10000000) {
            this.sources = response.data.inputSourceList;

            let selSource = getLoc('sel-source') || response.data.inputSourceList[0];
            //判断设备设置是否显示分辨率
            if(this.ids.some(id => {
              return id == getLoc('modelId');
            })) {
              if(selSource) {
                this.isShowResolution = selSource.type === 1 || selSource.type === 0;
              }
            }
            if(this.isShowResolution) {
              if(this.getSelectedRate.hasOwnProperty('width')) {
                this.rateValue = this.getSelectedRate.width + 'x' + this.getSelectedRate.height + '@' + this.getSelectedRate.rate + 'Hz';
              }
            }
            console.log(this.sources);
            let selected = this.sources.filter(s => {
              return s.id == selSource.id;
            });
            this.selectedSource = selected[0];
            this.getInputSourceQuality();
            this.getInputResolutionRatio();
          }
        });
      },
      /**
       * 获取设备画质信息
       */
      getInputSourceQuality() {
        htp.post(urls.getInputSourceQuality, { inputSourceId: this.selectedSource.id }).then(response => {
          if(response.status === 10000000) {
            let res = response.data;
            this.definition.light.value = res.brightness;
            this.definition.contrast.value = res.contrast;
            this.definition.saturability.value = res.saturation;
            this.definition.hue.value = res.hue;
            this.definition.Rvalue.value = res.RValue;
            this.definition.Gvalue.value = res.GValue;
            this.definition.Bvalue.value = res.BValue;
          }
        });
      },
      /**
       * 获取输入接口分辨率
       */
      getInputResolutionRatio() {
        if(this.selectedSource.status) {
          console.log(1);
          this.getedRateValue = this.selectedSource.width + 'x' + this.selectedSource.height + '@' + this.selectedSource.refresh + 'Hz';
        } else {
          this.getedRateValue = this.$t('common.Nosignal');
        }
      },
      /**
       * 选择输入源
       */
      selectSource() {
        this.$router.push({ name: 'selectsource', params: { selected: this.selectedSource } });
      },
      selectRate() {
        this.$router.push({ name: 'selresolution', params: { selected: this.getSelectedRate, selSource: this.selectedSource, type: 'enter' } });
      },
      //保存输入源
      save() {
        let params = {
          inputSourceId: this.selectedSource.id,
          brightness: parseInt(this.definition.light.value),
          contrast: parseInt(this.definition.contrast.value),
          saturation: parseInt(this.definition.saturability.value),
          hue: parseInt(this.definition.hue.value),
          RValue: parseInt(this.definition.Rvalue.value),
          GValue: parseInt(this.definition.Gvalue.value),
          BValue: parseInt(this.definition.Bvalue.value)
        };
        htp.post(urls.setInputSourceQuality, params);
      },
      cancel() {
        this.$router.go(-1);
      },
      refresh() {
        this.initEnterConfig();
      },
      reset() {
        this.definition.light.value = 50;
        this.definition.contrast.value = 50;
        this.definition.saturability.value = 50;
        this.definition.hue.value = 0;
        this.definition.Rvalue.value = 50;
        this.definition.Gvalue.value = 50;
        this.definition.Bvalue.value = 50;
        this.save();
      }
    }
  };
</script>
<style scoped lang="less">
  .vx-container {
    -webkit-overflow-scrolling: auto !important;
  }
  .setting_cont {
    margin-top: 25px;
    .vc-cell {
      border-top: 1px solid #4f5054; /*no*/
      &:first-child {
        border-top: none;
      }
    }

    .vx-progress {
      border-bottom: 1px #4f5054 solid; /*no*/
      background-color: #383940;
      padding-top: 20px;
      padding-bottom: 30px;
    }
    .progress-list {
      text-align: left;
      font-size: 24px;
      height: 60px;
      line-height: 60px;
      text-indent: 35px;
      .mask {
        color: #ddd;
        font-size: 24px;
      }
      .vx-progress {
        margin-top: 0;
        color: #a8a8aa;
      }
    }
  }

  .btns {
    display: flex;
    margin: 40px 100px 0;
    .m-btn {
      flex: 1;
      color: #ddd;
      height: 70px;
      line-height: 70px;
      background-color: #933e00;
      font-size: 34px;
      border-radius: 8px;
      text-align: center;
      text-indent: 0;
    }
    .mr {
      margin-right: 100px;
    }
  }
  .footer {
    height: 40px;
  }
</style>


